import React, {Component} from "react";
import {PropTypes} from "prop-types";

const preventDefaultWrapper = (handler) => (event) => {
        console.warn("prevent");
        console.warn(event);
        console.warn(handler);
    event.preventDefault();
    handler(event);
}

export default class MenuItem extends Component {

    constructor(props) {
        super(props);
    }

    handleOnClick(e, link, action) {
        console.warn("menuitem");
        console.warn(e);
        console.warn(link)
        console.warn(action);
        if (action)
            action(e);
    }

    render() {
        let entries = this.props.entries === undefined ? [] : this.props.entries;
        let brokenRestriction = this.props.availabilityRestrictions ? this.props.availabilityRestrictions
                .filter((restriction) => entries
                    .filter((entry) => entry.bundleType === restriction.menuItemType)
                    .length > restriction.cartBundleNumberLimit
                )
            :
            [];
        if (brokenRestriction.length <= 0) {
            return (
                <div className="u-padding-top-l">
                    <WhiteBoxWithShadow>
                        <LinkBox link={this.props.link}
                                 label={this.props.label}
                                 onClickHandlerProp={this.props.itemClickedHandler}
                                 linkClicked={this.handleOnClick}>
                            <div className="u-position-relative">
                                <div className="u-padding-left-l u-padding-right-l u-small-padding-left u-small-padding-right">
                                    <MenuItemBody>
                                        <IconCell icon={this.props.icon}/>
                                        <LabelCell label={this.props.label}/>
                                        <ButtonCell link={this.props.link}
                                                    label={this.props.label}
                                                    onClickHandlerProp={this.props.itemClickedHandler}
                                                    linkClicked={this.handleOnClick}
                                                    disabled={true}/>
                                    </MenuItemBody>
                                </div>
                            </div>
                        </LinkBox>
                    </WhiteBoxWithShadow>
                </div>);
        } else {
            return null;
        }
    }

}

const WhiteBoxWithShadow = (props) => {
    return <div className="g-white1-bg u-box-shadow">{props.children}</div>
}

const LinkBox = (props) => {
    return (
    <a href={props.link||"#"}
              onClick={ (e) => props.linkClicked(e, props.link, props.onClickHandlerProp)}
              aria-label={props.label}>
        <div className="u-box-shadow--s u-box-shadow-hover">{props.children}</div>
    </a>
    )
}

const MenuItemBody = (props) => {
    return <table className="u-table-fixed">
        <tbody>
        <tr>
            {props.children}
        </tr>
        </tbody>
    </table>
}

const IconCell = (props) => {
    return (
        <td className="opl-checkout__icon__cell">
            {props.icon && <span className={"g-icon g-icon--only g-icon--" + props.icon.toLowerCase().split('_').join("-")}/>}
        </td>)
}

const LabelCell = (props) => {
    return (
        <td className="u-padding-top-l u-padding-l u-padding-right-l">{props.label}</td>)
}

const ButtonCell = (props) => {
    return (
        <td className="opl-checkout__button__cell u-padding-top-l u-padding-l">
            {props.disabled ? (
                <span className="opl-product-addlink u-right u-small-right"/>
            ) : (
                <a href={props.link||"#"} className="opl-product-addlink u-right u-small-right"
                   onClick={ (e) => props.linkClicked(e, props.link, props.onClickHandlerProp)}
                   aria-label={props.label}>
                </a>
            )}
        </td>
    )
}

MenuItem.propTypes = {
    icon: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    availabilityRestrictions: PropTypes.arrayOf(PropTypes.shape({
        menuItemType: PropTypes.string,
        cartBundleNumberLimit: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    })).isRequired,
    addItemIcon: PropTypes.string.isRequired,
    type: PropTypes.string,
    entries: PropTypes.arrayOf(PropTypes.object),
    onclick: PropTypes.func
}
